/*  Beating Heart */


/* reset */

* {
    box-sizing: border-box;
}

div#heart_hover {
    /* border: 1px solid red; */
    float: left;
    margin: 100px;
    position: relative;
    transition: all 1s;
}

#heart_hover:hover {
    transform: scale(1.2);
}

#heart_hover>.left {
    background: red;
    width: 50px;
    height: 50px;
    position: absolute;
    transform: rotate(45deg) translateX(31px);
    bottom: 50px;
    left: -50px;
    border-radius: 50% 0 0 50%;
}

#heart_hover>.right {
    background: red;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    position: absolute;
    transform: rotate(45deg) translateY(31px);
    bottom: 50px;
    right: -50px;
    border-radius: 50% 50% 0 0;
}

#heart_hover>.bottom {
    background: red;
    width: 50px;
    height: 50px;
    transform: rotate(45deg);
}

div#heart_beat {
    float: left;
    margin: 100px;
    position: relative;
    animation: .5s heart_beat infinite alternate-reverse;
}

@keyframes heart_beat {
    0% {
        transform: scale(1);
    }
    100% {
        transform: scale(1.2);
    }
}

#heart_beat>.left {
    background: red;
    width: 50px;
    height: 50px;
    position: absolute;
    transform: rotate(45deg) translateX(31px);
    bottom: 50px;
    left: -50px;
    border-radius: 50% 0 0 50%;
}

#heart_beat>.right {
    background: red;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    position: absolute;
    transform: rotate(45deg) translateY(31px);
    bottom: 50px;
    right: -50px;
    border-radius: 50% 50% 0 0;
}

#heart_beat>.bottom {
    background: red;
    width: 50px;
    height: 50px;
    transform: rotate(45deg);
}